body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(149, 5, 18), rgb(53, 23, 145));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@font-face {
    font-family: 'Alien Robot Italic';
    font-style: normal;
    font-weight: normal;
    src: local('Alien Robot Italic'), url("/super-robofriends/static/media/Alien Robot Italic.08135464.woff") format('woff');
}

h1{
    font-family: 'Alien Robot Italic';
    font-weight: bolder;
    color: azure;
}
